<template>
  <div id="container">
    <h1>{{title}}</h1>
    <div id="boardOut">
      <ul id="board">
        <li v-for="grid in gridList" @click="chess(grid.index)">
          <span :class="{blackChessman:grid.chessed==1,whiteChessman:grid.chessed==2}" :index="grid.index">{{grid.chessNum}}</span>
        </li>
      </ul>
      <div id="chessEnd" v-if="isShow"></div>
    </div>
    <div id="btn"><button @click="restartChess">重新开始</button><button @click="withdraw" style="margin-left: 20px;">悔棋</button></div>
  </div>
</template>

<script>
  export default {
    name: 'gobang',
    data () {
      return {
        chessRowsCols:11,//棋盘行列
        chessmanNum:121,//棋子总数=行*列
        chessmanColor:true,//棋子颜色
        chessWhite:[],//白棋棋子
        chessBlack:[],//黑棋棋子
        chessPath:[],//棋子路径
        chessNums:0,//棋子数
        isShow:false,//判断胜负后不可继续落子
        title:'五子棋',
        gridList:[]
      }
    },
    created(){
        this.init();
    },
    methods:{
      chess(n){
          if (this.gridList[n].chessed==0) {//是否有棋子
              if (this.chessmanColor) {//黑子
                  this.gridList[n].chessed=1;
                  this.chessBlack.push(n);
              }else {//白子
                  this.gridList[n].chessed=2;
                  this.chessWhite.push(n);
              }
              this.chessNums=++this.chessNums;
              this.gridList[n].chessNum=this.chessNums;
              this.arrSort(this.chessBlack);//排序棋子数组
              this.arrSort(this.chessWhite);
              if (this.chessmanColor) {//判断胜利
                  this.isWin(this.chessBlack,'黑子赢！');
              }else {
                  this.isWin(this.chessWhite,'白子赢！');
              }
              this.chessmanColor=!this.chessmanColor;//黑白切换
              this.chessPath.push(n);//添加落子路径
          }
      },
      init(){
          this.gridList=[];
          for (let i=0;i<this.chessmanNum;i++){
              this.gridList.push({index:i,chessed:0,chessNum:null});
          }
      },
      withdraw(){//悔棋
          if (this.chessPath.length) {//判断是否有棋子
              const curChess=this.chessPath.pop();//获取最后一次落子
              this.gridList[curChess].chessed=0;//删除棋子
              this.gridList[curChess].chessNum=null;
              this.chessNums--;//棋子总数更新
              if (!this.chessmanColor) {//从棋子数组中删除
                  const index=this.chessBlack.indexOf(curChess);
                  if (index>-1) {
                      this.chessBlack.splice(index,1);
                  }
              }else {
                  const index=this.chessWhite.indexOf(curChess);
                  if (index>-1) {
                      this.chessWhite.splice(index,1);
                  }
              }
              this.chessmanColor=!this.chessmanColor;//黑白切换
              if (this.isShow) {//不可落子状态改为可以
                  this.title='五子棋';
                  this.isShow=false;
              }
          }
      },
      isWin(arr,mes){//胜利判断函数
        if (arr.length>=5) {
          for (let i=0;i<arr.length;i++) {
            if ((arr[i+4]-arr[i]==4) && ((arr[i]+arr[i+1]+arr[i+2]+arr[i+3]+arr[i+4])/5==arr[i+2])) {//横向
              this.title=mes;
              this.isShow=true;
              break;
            }
            let S=1;
            let R=1;
            let L=1;
            for (let j=i+1;j<arr.length;j++) {
              if (arr[j]-arr[i]==S*this.chessRowsCols) {//纵向
                S++;
                if (S==5) {
                  this.title=mes;
                  this.isShow=true;
                  break;
                }
              }
              if (arr[j]-arr[i]==R*(this.chessRowsCols+1)) {//右斜
                R++;
                if (R==5) {
                  this.title=mes;
                  this.isShow=true;
                  break;
                }
              }
              if (arr[j]-arr[i]==L*(this.chessRowsCols-1)) {//右斜
                L++;
                if (L==5) {
                  this.title=mes;
                  this.isShow=true;
                  break;
                }
              }
            }
          }
        }
      },
      arrSort(arr){//数组排序
        for(let i=arr.length;i>0;i--) {
          if (arr[i]<arr[i-1]) {
            let temp=arr[i];
            arr[i]=arr[i-1];
            arr[i-1]=temp;
          }
        }
      },
      restartChess(){//重新开始
        this.isShow=false;
        this.chessmanColor=true;
        this.chessWhite=[];
        this.chessBlack=[];
        this.chessPath=[];
        this.chessNums=0;
        this.init();
        this.title='五子棋';
      }
    }
  }
</script>

<style scoped>
  #container {
    width: 700px;
    height: 700px;
  }
  h1 {
    text-align: center;
  }
  #boardOut {
    width:500px;
    height:500px;
    margin:20px auto;
    border:2px solid #000;
    background:#fdda9c;
    position:relative;
    overflow:hidden;
  }
  #board {
    width: 550px;
    height: 550px;
    position: absolute;
    left: -25px;
    top: -25px;
  }
  #board li {
    width: 50px;
    height: 50px;
    position: relative;
    float: left;
  }
  #board li::before {
    content: '';
    width: 100%;
    border-bottom: 1px solid #333;
    position: absolute;
    left: 0;
    top: 50%;
  }
  #board li::after {
    content: '';
    height: 100%;
    border-right: 1px solid #333;
    position: absolute;
    top: 0;
    left: 50%;
  }
  #chessEnd {
    position: absolute;
    left: 0;
    top: 0;
    width: 500px;
    height: 500px;
    background-color: transparent;
    z-index: 100;
  }
  #btn {
    text-align: center;
  }
  #btn button {
    width: 80px;
    height: 40px;
  }
  .blackChessman,.whiteChessman {
    width: 46px;
    height: 46px;
    line-height: 46px;
    text-align: center;
    position: absolute;
    top: 2px;
    left: 2px;
    background-color: #000;
    color: #fff;
    font-weight: bold;
    border-radius: 50%;
    z-index: 10;
  }
  .whiteChessman {
    background-color: #fff;
    color: #000;
  }
</style>



