<template>
  <div id="container">
    <h1>{{title}}</h1>
    <div id="boardOut">
      <div id="topNum">
        <div v-for="n in 10" class="">{{n}}</div>
      </div>
      <div id="leftNum">
        <div v-for="n in 10" class="">{{n}}</div>
      </div>
      <div id="board" v-if="restart">
        <div v-for="n in 100" :x="n%10||10" :y="Math.ceil(n/10)" @click="move"></div>
        <span id="block" v-if="isShow" :style="{left:(x-1)*50+'px',top:(y-1)*50+'px'}" :x="x" :y="y"></span>
      </div>
    </div>
    <div id="btn">
      <button @click="randomWall">随机造墙</button>
      <button @click="restartBox" style="margin-left: 20px;">重新加载界面</button>
    </div>
    <p id="mes">点击网格内任意地点，小方块则会自动寻路并且移动到点击的位置，中间如有墙则会绕行</p>
  </div>
</template>

<script>
  export default {
    name: 'block',
    data () {
      return {
        title:'移动的小方块',
        restart:true,
        x:Math.floor(Math.random()*10+1),
        y:Math.floor(Math.random()*10+1),
        timer:null,
        isShow:true
      }
    },
    methods:{
      randomBlock(){//随机生成小方块
          const oBlock=window.document.getElementById('block');
          const x=Math.floor(Math.random()*10+1);
          const y=Math.floor(Math.random()*10+1);
          oBlock.style.left=(x-1)*50+'px';
          oBlock.style.top=(y-1)*50+'px';
          this.x=x;
          this.y=y;
          oBlock.setAttribute('x',x);
          oBlock.setAttribute('y',y);
      },
      move(event){//移动
          clearInterval(this.timer);
          let target=event.target;
          let result=this.searchRoad(this.x,this.y,target.getAttribute('x')-0,target.getAttribute('y')-0);
          this.timer=setInterval(()=>{
              if (result.length!==0 && result) {
                const oBlock=window.document.getElementById('block');
                let item=result.shift();
                this.x=item.x;
                this.y=item.y;
                oBlock.style.left=(item.x-1)*50+'px';
                oBlock.style.top=(item.y-1)*50+'px';
                oBlock.setAttribute('x',item.x);
                oBlock.setAttribute('y',item.y);
              }else {
                clearInterval(this.timer);
              }
          },1000);
      },
      searchRoad(startX,startY,endX,endY){//寻路算法
          let openList=[],
              closeList=[],
              result=[],
              resultIndex,
              oBoard=window.document.getElementById('board'),
              oDivs=oBoard.querySelectorAll('div');
          if (startX==endX && startY==endY) {
            alert('目标点和起点是同一个点！');
            return false;
          }else if (oDivs[(endY-1)*10+endX-1].className.indexOf('wall')!==-1) {
            alert('目标点是墙！');
            return false;
          }
          openList.push({x:startX,y:startY,G:0});
          do {
              let curPoint=openList.pop();
              closeList.push(curPoint);
              let surPoint=this.SurPoint(curPoint);
              for (let i in surPoint) {
                  let item=surPoint[i];
                  if (item.x>=1 && item.x<=10 && item.y>=1 && item.y<=10 && oDivs[(item.y-1)*10+item.x-1].className.indexOf('wall')==-1 && !this.existList(item,closeList)) {//不是障碍物，不在关闭列表
                    let g=curPoint.G+10;
                    if (!this.existList(item,openList)) {//不在开启列表
                        item['H']=Math.abs(endX-item.x)*10+Math.abs(endY-item.y)*10;
                        item['G']=g;
                        item['F']=item.G+item.H;
                        item['parent']=curPoint;
                        openList.push(item);
                    }else {//在开始列表
                        let index=this.existList(item,openList);
                        if (g<openList[index].G) {
                            openList[index].parent=curPoint;
                            openList[index].G=g;
                            openList[index].F=g+openList[index].H;
                        }
                    }
                  }
              }
              if (openList.length==0) {//开启列表空了
                  alert('无路可走！');
                  break;
              }
              openList.sort(function (a,b) {//根据F值降序排序，保证每次所取的F值最小
                  return b.F-a.F;
              })
          }while (!(resultIndex=this.existList({x:endX,y:endY},openList)));//当目标点在开启列表时，寻路完成
          if (!resultIndex) {
              result=[];
          }else {
              let curObj=openList[resultIndex];
              do {//添加路径
                  result.unshift({x:curObj.x,y:curObj.y});
                  curObj=curObj.parent;
              }while (curObj.x!=startX || curObj.y!=startY);
          }
          return result;//返回路径数组
      },
      SurPoint(curPoint){//获取周围的点
          let x=curPoint.x,
              y=curPoint.y;
          return [
            {x:x-1,y:y},
            {x:x,y:y+1},
            {x:x+1,y:y},
            {x:x,y:y-1}
          ]
      },
      existList(point,list){//是否在列表里
          for (let i in list) {
              if (point.x==list[i].x && point.y==list[i].y) {
                  return i;
              }
          }
          return false;
      },
      randomWall(){
        clearInterval(this.timer);
        for (let i=0;i<10;i++) {
          const x=Math.floor(Math.random()*10+1),
                y=Math.floor(Math.random()*10+1),
                oBoard=window.document.getElementById('board'),
                oDivs=oBoard.querySelectorAll('div');
          if (!(x==this.x && y==this.y) && oDivs[(y-1)*10+x-1].className.indexOf('wall')==-1) {
            oDivs[(y-1)*10+x-1].className='wall';
          }
        }
      },
      restartBox(){//重新开始
        clearInterval(this.timer);
        this.restart=false;
        this.isShow=false;
        this.$nextTick(function () {
          this.restart=true;
          this.isShow=true;
        });
        this.randomBlock();
      }
    }
  }
</script>

<style scoped>
  #container {
    width: 700px;
    height: 700px;
  }
  h1 {
    text-align: center;
  }
  #boardOut {
    width:550px;
    height:550px;
    margin:0 auto;
    box-sizing: border-box;
    position:relative;
    overflow:hidden;
  }
  #board {
    width: 500px;
    height: 500px;
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: #fff;
    border:2px solid #000;
    overflow:hidden;
  }
  #board .wall {
    background-color: #999;
  }
  #topNum {
    position: absolute;
    top: 0;
    right: 0;
    width: 500px;
    height: 50px;
  }
  #leftNum {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 50px;
    height: 500px;
  }
  #topNum>div,#leftNum>div {
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    float: left;
  }
  #board>div {
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    float: left;
    border: 1px solid #999;
    box-sizing: border-box;
  }
  #block {
    width: 50px;
    height: 50px;
    background-color: skyblue;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    transition: all 1s ease;
  }
  #btn {
    margin-top: 10px;
    text-align: center;
  }
  #btn button {
    width: 100px;
    height: 40px;
  }
  #mes {
    text-align: center;
    margin-top: 10px;
  }
</style>



